.header {
  //background-color: #fff;
  font-size: 1rem;
  z-index: 1;
  
  /*&.sticky-top {
    background-color: #fff;

    .navbar-brand  svg {
      height: 28px;
    }
  }*/

  &.fixed-top {
    margin-top: 0 !important;
    position: fixed !important;
    background-color: #fff;
    z-index: 2;

    .navbar-brand  svg {
      height: 30px;
    }
  }

  .navbar-brand {
    svg {
      height: 62px;
      transition: height .3s ease-in-out;

      @include media-breakpoint-down('xl') {
        height: 50px;
      }
    }
  }

  .navbar-toggler {
    background: url('../img/menu.svg') center center no-repeat;
    height: 24px;
    border: none;

    &:focus {
      box-shadow: none;
    }
  }
}


.nav-mobile {
  
  &__login {
    
    .icon {
      color: #13a5ef;
    }
  }
  &__bottom {
    a {
      height: 72px;
      opacity: .3;

      &:focus,
      &:active,
      &:hover {
        opacity: 1;

        .icon {
          color: #13a5ef;
        }
      }

      .icon {
        //filter: invert(22%) sepia(13%) saturate(1617%) hue-rotate(183deg) brightness(96%) contrast(91%);
        height: 24px;
        width: 24px;
      }
    }
    
  }
}