.case-study-slider {

  &__item {
    background: linear-gradient(151.62deg, #13A5EF 0%, #4079FF 99.09%);
    background-size: cover !important;

    &:after {
      content: '';
      display: block;
      padding-bottom: 135%;

      @include media-breakpoint-up('lg') {
        padding-bottom: 100%;
      }
    }



    &:hover {
      .case-study-slider__description {
        max-height: 500px;
      }
    }
  }

  &__description {
    max-height: 0;
    transition: max-height .45s ease-in-out;
    overflow: hidden;
  }
}