body {
	//font-size: 1rem;
	font-family: 'Montserrat', sans-serif;
	position: relative;
	width: 100%;
	overflow: hidden;

	@include media-breakpoint-up('lg') {
		font-size: 1.125rem;
	}
}

.color-gradient {
	background: linear-gradient(45deg, #313E5B 0%, #13A5EF 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.img-drop-shadow {
	filter: drop-shadow(0px 30px 100px rgba(16, 53, 135, 0.15));
}

/*.icon {
	display: inline-block;
	vertical-align: middle;
}*/

a:hover .icon {
	//filter: invert(48%) sepia(92%) saturate(968%) hue-rotate(170deg) brightness(94%) contrast(99%);
	color: #13A5EF;
}

.nav-bb {
	@include media-breakpoint-up('xl') {
		a,
		.nav-link {
			position: relative;
			display: inline-block;

			&:after {
				content: '';
				position: absolute;
				height: 4px;
				width: 100%;
				border-radius: 4px;
				background: #13A5EF;
				opacity: 0;
				//background: rgba(19, 165, 239, 0);
				transition: opacity .15s ease-in-out;
				left: 0;
				bottom: 0;
			}

			&:hover:after {
				opacity: 1;
				//background: rgba(19, 165, 239, 1);
			}

		}
	}
}

.nav-tabs .nav-link {
	font-size: 1.125rem;
	&.active {
		position: relative;
		display: inline-block;

		&:after {
			content: '';
			position: absolute;
			height: 4px;
			width: 100%;
			border-radius: 4px;
			background: #13A5EF;
			transition: opacity .15s ease-in-out;
			left: 0;
			bottom: 0;
		}
	}
}

.link_icon {
	position: relative;
	display: inline-block;

	&:after {
		content: '';
		position: absolute;
		height: 4px;
		width: 100%;
		border-radius: 4px;
		background: #13A5EF;
		opacity: 0;
		//background: rgba(19, 165, 239, 0);
		transition: opacity .15s ease-in-out;
		left: 0;
		bottom: -10px;
	}

	&:hover:after {
		opacity: 1;
		//background: rgba(19, 165, 239, 1);
	}
}

.overflow-x-scroll {
	overflow: scroll hidden;
}
.disable-scrollbars {
	scrollbar-width: none; /* Firefox */
	-ms-overflow-style: none;  /* IE 10+ */

	&::-webkit-scrollbar {
		background: transparent; /* Chrome/Safari/Webkit */
		width: 0;
		height: 0;
	}
}

h1, .h1 {
	@include media-breakpoint-up('lg') {
		font-size: 4.5rem;
	}
}

h3, .h3 {
	@include media-breakpoint-up('lg') {
		font-size: 4rem;
	}
}

h4, .h4 {
	@include media-breakpoint-up('lg') {
		font-size: 2.625rem;
	}
}

h5, .h5 {
	@include media-breakpoint-up('lg') {
		font-size: 1.5rem;
	}
}

.display-3 {
	//font-size: 4rem;

	@include media-breakpoint-up('lg') {
