// fix
/*
.swiper {
	overflow: visible;
}*/

.swiper-slide {
	@include media-breakpoint-down('sm') {
		width: 80%;
	}
}


.swiper-pagination-bullet {
	display: inline-block;
	width: 8px;
	height: 8px;
	border-radius: 100%;
	background-color: rgba(49, 62, 91, 0.24);
	margin: 0 12px;

	&.swiper-pagination-bullet-active {
		background-color: #13A5EF;
	}
}

.swiper-button {
	display: inline-block;
	width: 52px;
	height: 52px;

	@include media-breakpoint-up('lg') {
		width: 72px;
		height: 72px;
	}

	.icon {
		width: 32px;
		height: 32px;

		@include media-breakpoint-up('lg') {
			width: 42px;
			height: 42px;
		}
	}

	&:not(.swiper-button-disabled) {
		//filter: invert(48%) sepia(92%) saturate(968%) hue-rotate(170deg) brightness(94%) contrast(99%);
		color: #13A5EF;
	}
}

.square {
	padding-bottom: 100%;
}

.hover-block {

	&:hover,
	&:focus,
	&:active {
		box-shadow: 0px 30px 100px rgba(16, 53, 135, 0.15);
	}

	&__title {
		font-size: 1.5rem;

		@include media-breakpoint-up('lg') {
			font-size: 3rem;
		}
	}

	&__icon {
		color: #13a5ef;
	}
}

.list-tick {
	padding-left: 0;

	list-style: none;

	>li {
		padding-left: 2.5rem;
		position: relative;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			display: inline-block;
			height: 1.5rem;
			width: 1.5rem;
			//background-image: url(http://placehold.it/32?text=fallback); // Fallback PNG
			background: url(../img/tick.svg) no-repeat;
			background-size: contain;
		}
	}
}

.accordion {

	&-item {
		border: none;

		.accordion-button {
			border: none;
			font-size: 1.25rem;

			@include media-breakpoint-up('lg') {
				font-size: 2.625rem;
			}

				&::after {
				align-self: baseline;
				@include media-breakpoint-up('lg') {
					width: 3rem;
					height: 3rem;
					background-size: 3rem;
				}
			}

			&:not(.collapsed) {
				box-shadow: none;
			}
		}
	}
}

.person {

	&__image {
		max-width: 104px;

		@include media-breakpoint-up('lg') {
			max-width: none;
		}
		filter: drop-shadow(0px 15px 30px rgba(49, 62, 91, 0.15));
	}

	&__title {
		font-size: 1.25rem;
		line-height: 1.4;

		@include media-breakpoint-up('lg') {
			font-size: 1.5rem;
		}
	}

	&__post {
		font-size: 1rem;

		@include media-breakpoint-up('lg') {
			font-size: inherit;
		}
	}
}

.wh-15 {
	width: 1.5rem;
	height: 1.5rem;
}

.was-validated .form-control:invalid + label, .form-control.is-invalid + label {
	color: $red;
}
.was-validated .form-control:valid + label, .form-control.is-valid + label {
	color: $green;
}