.clients-slider {

  &__item {

    &.collapsed {
      border-color: #13A5EF;
    }
  }

  &__name {
    font-size: .875rem;
    @include media-breakpoint-up('lg') {
      font-size: 1.5rem;
    }
  }

  &__post {
    font-size: .75rem;
    opacity: .6;

    @include media-breakpoint-up('lg') {
      font-size: 1.125rem;
    }
  }

  &__picture {
    background: linear-gradient(0deg, #F3F4F7, #F3F4F7);
    //min-width: 56px;
    width: 56px;
    height: 56px;

    @include media-breakpoint-up('lg') {
      width: 128px;
      height: 128px;
    }
  }

  &__description {
    //overflow: hidden;


    &:not(.show) {
      height: 156px;
      display: block;
    }
  }

  &__description-toggle {
    color: #838B9D;

    
    &:focus,
    &:active {
      color: #838B9D;
      text-decoration: initial;
    }

    &:not(.collapsed) {
      font-size: 0;
      color: #13A5EF;

      &:after {
        content: 'Read less';
        font-size: initial;
      }
    }
  }

/*  .swiper-slide .clients-slider__item {
    position: absolute;
  }*/
}